import request from '@/utils/request'

const baseUrl = '/api/admin/tag'

const tagApi = {}

tagApi.findAll = (params) => {
  return request({
    url: `${baseUrl}`,
    params: params,
    method: 'get'
  })
}

tagApi.page = (params) => {
  return request({
    url: `${baseUrl}/page`,
    params: params,
    method: 'get'
  })
}

tagApi.get = tagId => {
  return request({
    url: `${baseUrl}/${tagId}`,
    method: 'get'
  })
}

tagApi.update = (params) => {
  return request({
    url: `${baseUrl}/update`,
    data: params,
    method: 'put'
  })
}

tagApi.create = tag => {
  return request({
    url: `${baseUrl}/save`,
    data: tag,
    method: 'post'
  })
}

tagApi.delete = tagId => {
  return request({
    url: `${baseUrl}/${tagId}`,
    method: 'delete'
  })
}

export default tagApi
